.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  overflow: auto;
  align-items: center;
}

.timer {
  width: calc(100% - 32px);
  height: 200px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 42px;
  font-family: monospace;
  margin: 8px;
  border: 4px solid black;
  max-width: 400px;
}

.button {
  margin: 8px;
  border: 4px solid black;
  box-shadow: 0px 2px 6px 0px #000d;
  background: none;
  font-size: 20px;
  padding-top: 16px;
  padding-bottom: 16px;
  width: calc(100% - 32px);
  max-width: 400px;
  cursor: pointer;
}

.button:hover {
  background: #000;
  color: #ddbbbb;
}

.button:active {
  box-shadow: 0px 0px 0px 0px #000;
  background: none;
  color: #000;
}

.info {
  margin: 8px;
  background: none;
  font-size: 20px;
  padding-top: 16px;
  padding-bottom: 16px;
  width: calc(100% - 32px);
  max-width: 400px;
}
